@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,300;0,400;0,500;1,200;1,300;1,400;1,500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&display=swap');
html {
  scroll-behavior: smooth;
}
.App {
  text-align: center;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
  a:hover{
    animation: a-hover-border  1 5s linear;
  }
  .App-cv:hover{
    animation: color-change 1 5s linear;
  }
}

.App-header {
  position: fixed;
  left: 0;
  top: 0;
  min-width: 4vmin;
  min-height: 4vmin;
  padding-left: 2vmin;
  padding-right: 2vmin;
  padding-top: 2vmin;
  padding-bottom: 2vmin;
  font-size: calc(10px + 2vmin);
  color: white;
  z-index: 11;
}

.App-gototop {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 4vmin;
  min-height: 2vmin;
  padding-left: 1vmin;
  padding-right: 1vmin;
  padding-top: 1vmin;
  padding-bottom: 1vmin;
  margin: 2px;
  font-size: calc(10px + 2vmin);
  color: white;
  background-color: red;
  z-index: 11;
  cursor: pointer;
}
.App-header:hover {
cursor: pointer;
}

.App-link {
  color: #61dafb;
}

.App-main {
  position: relative;
  width: 100%;
  max-width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: left;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-background {
  width: 100%;
  max-width: 100vw;
  height: 100vh;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  object-fit: cover;
  overflow: hidden; 
  z-index: 1;
}
.App-scrolldown{
  position: absolute;
  width: fit-content;
  left: 49%;
  bottom: 5%;
  z-index: 3;
}
.App-scrolldown:hover{
  cursor: pointer;
}

.App-intro {
  max-width: 100vw;
  position: absolute;
  padding-top: 11% !important;
  left: 20%;
  width: 70%;
  height: 70%;
  display: flex;
  flex-direction: row;  
  flex-wrap: wrap-reverse;
  justify-content: space-around;
  min-height:  100vh;
  padding-right: 2vmin;
  font-size: calc(10px + 2vmin);
  color: white;
  z-index: 3;
}
.App-myphoto{
  max-width: 300px;
  min-width: 150px;
  height: auto;
  background-position: center center;
  background-repeat: no-repeat;
  overflow: hidden;
  border: white;
  border-width: 4px;
  border-style: solid;
  border-radius: 5px;
  z-index: 2;
}
.App-intro-textcontainer{
  width: 400px;
  z-index: 2;
}
.App-hi{
  font-size: 8vmin;
  text-align: left;
  margin-bottom: 20px;
  font-family: 'Montserrat';
}
.App-myname{
  font-size: 4vmin ;
  text-align: left;
  margin-bottom: 15px;
  font-family: 'Montserrat';
  font-weight: 200;
}
.App-mybio{
  text-align: left;
  font-family: 'Roboto';
  font-weight: 300;
}
.App-cv{
  font-family: 'Roboto';
  font-weight: 300;
  background-color: rgb(146, 146, 36);
  border-radius: 5px;
  color: white;
  width: fit-content;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 15px;
  padding-right: 15px;
  margin-top: 15px;
}

h1{
  text-align: center;
  font-family: 'Roboto';
  font-weight: 300;
}
.App-navigator {
  background-color: #001e5a95;
  position: fixed;
  display: grid;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  max-width: 100vw;
  height: 100%;
  
align-content: center;
}
.App-navigator-container{
  margin:10% ;
  display:inline-flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-navigator-component{
  margin-left: 2vmin;
  margin-right: 2vmin;
  height: fit-content;
  padding: 3vmin;
  border-width: 5px;
}
.App-projects {
  position: relative;
  width: 100%;
  max-width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  background-color: #f1f1f1;
  padding-bottom: .83rem;
}
.App-projects-container{
  align-self: center;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: 'center';
  font-size: 14px;
}
.App-projects-components{
  align-self: center;
  height:150px;
  width: 150px;
  margin: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 14px;
  box-sizing: border-box;
  color: rgba(0, 0, 0, 0.87);
  display: block;
  font-family: 'Monserrat', 'sans-serif';
  font-size: 13px;
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 24px;
  padding-bottom: 24px;
  text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: white;
  border-radius: 5px;
}
.App-projects-components:hover{
  cursor: pointer;
}
.App-projects-text{
  min-height: 50px;
  margin-top: 5px;
  font-size: medium;
}
.App-projects-image{
  align-self: center;
  width: 100px;
  height: auto;
}
.App-showcase-container{
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.App-showcase-image{
  width: 500px !important;;
  max-width: 80vw;
  height: auto;
  border: black 2px solid;
}
.App-showcase-text{
  width: 500px;
}
.App-showcase-title{
  font-family: 'Montserrat';
  font-weight: 200;
}
.App-showcase-subtitle{
  font-family: 'Roboto';
  font-weight: 300;
  font-size: 14px;
}
.App-expertise {
  position: relative;
  width: 100%;
  max-width: 100vw;
  min-height: 100vh;
  padding-top: .83rem;
  padding-bottom: .83rem;
  font-size: calc(10px + 2vmin);
  background-color: aliceblue;
}
.App-expertise-container {
  align-self: center;
  margin-left: 10vmin;
  margin-right: 10vmin;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: 'center';
  font-size: calc(10px + 2vmin);
 
}

.App-expertise-components {
  height: 200px;
  width:200px;
  margin: 25px;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  background-color: white;
}
.App-expertise-image{
 align-self: center;
 max-width: 80vw;
 height: auto;
}
a:hover{
  background-color: rgb(207, 0, 0);
  color:white;
  text-emphasis: none;
  cursor: pointer;
}

a{
  color:white;
  text-decoration: none;
}
.App-about{
  position: relative;
  width: 100%;
  max-width: 100vw;
  min-height: 100vh;
  padding-top: .83rem;
  padding-bottom: .83rem;
  font-size: calc(10px + 2vmin);
  background-color: snow;
}

.App-about-intro{
  align-self: center;
  text-align: center;
  font-family: 'Roboto';
  font-weight: 300;
  font-size: large;
  line-height: 30px;
}
.App-contact{
  position: relative;
  width: 100%;
  min-height: 80vh;
  padding-top: .83rem;
  padding-bottom: .83rem;
  font-size: calc(10px + 2vmin);
  background-color: #f1f1f1;
  display: flex;
  flex-direction: column;
  
}
.App-about-pic-container{
  justify-content: center;
}
.App-about-pic{
  max-height: 200px;
  border-radius: 50%;
  margin: 10px;
  align-self: center;
  height: auto;
  border: whitesmoke;
  border-width:2px ;
  border-style: solid;
}
.App-contact-icon-container{
  align-self: center;
  font-size: 50px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;

}
.App-contact-icon{
  align-self: center;
  margin: 50px;
  padding:20px;
  border-radius: 5px;

}
.App-contact-icon:hover{
  cursor: pointer;
  background-color: red;
  color: whitesmoke;
}
.App-footer {
  background-color: #3c7bf8;
  min-height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(2px + 2vmin);
  color: white;
  text-align: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes a-hover-border {
  from {
   
  }
  to {
 
  }
}


@keyframes color-change {
  from {
   background-color: tomato;
  }
  to {
 background-color: teal;
  }
}
